.dialog {
    width: 75%;
    border-radius: 0px 8px 8px 0px;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.5);
}

.header {
    height: 160px;
    display: flex;
    flex-direction: column;
}

.fon {
    height: 32px;
    background-color: #005475;
    border-radius: 0px 8px 0px 0px;
}

.pomoshnikSearch {
    height: 48px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
    box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
}

.pomoshnik {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    align-items: center;
}

.iconBack:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 50%;
    cursor: pointer;
}

.search {
    -webkit-appearance: none;
    /* Удаляет стандартные стили WebKit */
    width: 10vw;
    /* Регулирует ширину */
    height: 50%;
    /* Регулирует высоту */
    border-bottom: 1px solid #ccc;
    /* Добавляет границу */
}


.spanPomoshnik {
    align-self: flex-start;
    font-family: "Montserrat";
    font-size: 12px;
    position: relative;
    font-weight: 600;
}

.spanPomoshnik span {
    color: rgb(58, 58, 58);
}

.spanPomoshnik::after {
    content: 'КРАТКОСРОЧНАЯ ЦЕЛЬ';
    position: absolute;
    width: 200px;
    left: 0;
    bottom: -15px;
    font-weight: 600;
    font-size: 12px;
}

.editText {
    padding: 0.1% 2%;
    height: 40px;
    width: 100%;
    background-color: #ffff;
    display: flex;
    flex-direction: row;
    justify-content: end;
    align-items: center;
    box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
}

.one {
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.one img:hover,
.two img:hover,
.three img:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
}

.blockSelect {
    position: relative;

    &>img:hover {
        background-color: rgb(240, 240, 240);
        border-radius: 20%;
    }
}

.option {
    position: absolute;
    display: none;
    white-space: nowrap;
    right: 1%;
    background-color: #ffff;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    padding: 5px;
    padding-bottom: 8px;
}

.option li:first-child {
    margin-top: 0px;
}

.option li {
    margin-top: 7px;
    position: relative;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 12px;
    z-index: 10000;
}

.option li:hover {
    background-color: rgb(240, 240, 240);
    z-index: 10000;
}

.option li:before {
    content: '';
    position: absolute;
    height: 1px;
    width: 100%;
    bottom: -4px;
    left: 0;
    background-color: rgb(240, 240, 240);
    z-index: 10000;
}

.blockSelect:hover .option {
    display: block;
    z-index: 10000;
}

.two {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-left: 2%;
}

.three {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-left: 2%;
}

.date {
    height: 60%;
    min-width: 200px;
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: row;
    justify-content: space-around; 
    align-items: center;
    /* text-align: center;
    vertical-align: middle; */
    margin-right: auto;
}

.image:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
}

.image {
    height: auto;
    cursor: pointer;
    margin-right: 0.5%;
}

.main {
    position: relative;
    flex: 1 0 120px; 
    overflow-y: auto;
    background-color: #F0F0F0;
    padding-top: 1%;
    padding-bottom: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1%;
}

.editorContainer {
    margin-top: 20px;
    position: relative;
    /* Это позволит дочернему элементу быть позиционированным относительно этого контейнера */
    margin-bottom: 16px;
    /* Расстояние между редакторами */
}

.deleteIcon {
    position: absolute;
    /* Позиционируем элемент абсолютно */
    top: -25px;
    /* Расстояние от верхней части контейнера */
    right: -30px;
    /* Расстояние от правой части контейнера */
    cursor: pointer;
    /* Изменяем курсор при наведении на иконку */
}

.iconAdd {
    position: relative;
    z-index: 10000;
}

.iconAdd:hover::after {
    z-index: 10000;
    content: "Создать";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.iconSave {
    position: relative;
    z-index: 10000;
    margin-left: 10px;
}

.iconSave:hover::after {
    z-index: 10000;
    content: "Сохранить";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.Teaxtaera {
    min-width: 50%;
    min-height: 10vw;
    background-color: #FFFFFF;
    padding: 1%;
    padding: 1%;
    resize: none;
}

textarea::placeholder {
    color: #B4B4B4;
    font-style: italic;
    font-size: 12px;
    font-family: 'Montserrat';
    padding-top: 3%;
    padding-left: 3%;
}

.add {
    height: 50px;
    width: 350px;
    background-color: #ffff;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1%;
    column-gap: 5%;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.25);
    align-self: flex-start;
    margin-left: 25%;
    cursor: pointer;
    transition: all 0.5s ease;
}

.add:hover {
    background-color: #B4B4B4;
    color: #FFFF;
}

.nameButton {
    font-size: 14px;
    font-family: 'Montserrat';
    color: #3A3A3A;
    font-weight: 400;
}

.add:hover span {
    font-size: 14px;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #FFFF;
}

.add div {
    color: #3A3A3A;
    font-size: 14px;
    font-family: 'Montserrat';
    font-weight: 400;
}

.add svg {
    transition: fill 0.3s ease;
    /* Добавляем анимацию для цвета SVG */
}

.add:hover svg path {
    fill: #FFFFFF;
    /* Меняем цвет SVG при наведении */
}

.text {
    font-family: "Montserrat";
    font-size: 12px;
}

.selectHeader {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    margin-bottom: 15px;
}

.textSelectHeader:hover .textSelectHeaderSpan {
    color: #388cad;
    font-weight: 600;
    transition: all 0.5s ease;
}

.textSelectHeader:before {
    display: none;
    content: '';
    width: 100%;
    height: 15%;
    position: absolute;
    bottom: -50%;
    left: 0;
    background-color: #388cad;
}

.textSelectHeader:hover::before {
    display: block;
}

.textSelectHeader {
    position: relative;
}

.textSelectHeaderSpan {
    color: rgb(58, 58, 58);
    font-size: 16px;
    font-family: 'Montserrat';
    font-weight: 400;
    transition: all 0.5s ease;
}

.active {
    font-size: 16px;
    font-family: 'Montserrat';
    color: #005475 !important;
    font-weight: 600 !important;
}

.activeTextSelectHeader::before {
    display: block;
    background-color: #005475;
}

.select {
    font-family: "Montserrat";
}

.activeSelect{
    color: #005475;
}
.completed{
    color: grey;
}
.draft{
    color: black;
}